.carousel-container{
    
    display: grid;
    grid-template-columns: 25% 75%;
    background-color: black;
    padding: 30px 0 30px 0;

}

.carousel-title {
    width: auto;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 30px;
    padding: 0 100px 0 100px 0;
}

.slide-container {
    height: 100%;
    max-width: 100%;
}

.slide-container > img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    border-radius: 5%;
}


.slide-desc {
    background-color: black;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 70%;
    padding-left: 30px;
    border-top-left-radius: 20px;
    opacity: 0.8;
}

.slide-desc > span {
    color: white;
    display: block;
    font-size: 22px;
    padding: 20px 0 20px 10px
}

.BrainhubCarousel__arrows {
    background-color: white;
    border-radius: 50%;
    margin: 20px;
}

.BrainhubCarousel__arrows > span {
    border-color: black;   
}

.BrainhubCarousel__arrows:hover {
    background-color: white !important;
}

.BrainhubCarousel__arrows:hover >span {
    border-color: black !important;
}

@media only screen and (max-width: 960px) {
    .carousel-container {
        display: flex;
        flex-direction: column;
    }
    .carousel-title {
        width: 100%;
        text-align: center;
        padding: 50px 0 50px 0;
    }

    .slide-container {
        width: 100%;
    }
}